<template>
    <div>
        <!--规则弹框-->
        <van-popup closeable
                v-model="isShow"
                :close-on-click-overlay='false'
                round
                :style="{ height:'auto',width:'80%',padding:'38px 16px 16px'}" @close="$emit('closeRule')">
        <div class='rule_title'>活动规则</div>
        <div class='rule_text'>
            <div>1、本次活动时间为2022年4月25日11时0分至2022年5月24日17时0分；</div>
            <div>2、每位用户最多可获得3次拆盲盒机会；</div>
            <div>3、中奖用户可在“中奖弹窗”或“我的奖品”页面填写收货地址，如在活动时间内未填写收货地址，则视为放弃奖品；</div>
            <div>4、为保证发货效率，收货地址一经提交不予修改；</div>
            <div>5、若平台发现用户存在批量注册、恶意刷奖等行为，平台有权取消其中奖资格且不予发货；</div>
            <div>6、本活动最终解释权归老板电器所有。</div>
        </div>
        </van-popup>
    </div>
</template>
<script>
 

export default {
    data() {
        return {
            isShow:true
        }
    },
}
</script>
<style lang="scss" scoped>
.rule_title {
  font-size: 36px;
  font-family: PingFang SC;
  font-weight: 500;
  color: #333333;
  margin-bottom: 28px;
}
.rule_text {
  text-align: left;
  font-size: 30px;
  font-family: PingFang SC;
  font-weight: 400;
  color: #666666;
  line-height: 48px;
}
</style>



