        <div class='people-total'>累计<span class='num'>{{totalPeople}}</span>人次参与抽奖</div>
        <!--获奖快报-->
        <scroll-reward :swiperArr="swiperArr"/>
        <!--活动规则-->
        <rule-pop v-if="displayRuleBox" @closeRule="displayRuleBox=false"/>
    </div>
</template>
