        userId: '',
        startTime:'2022-02-10 14:07:00',
        endTime:'2022-08-07 17:00:00',
        isInActivityTime:'inner',
        isShakeBox:true,//礼物图片class控制
        lottery: {
            unpackingCount: 1, //剩余拆盒次数
            shareCount: 0, //分享次数
        },
        totalPeople: 200, //累计参与人数
        displayRuleBox:false,//规则弹框
        isLoading:false,
        displayPoster:false,
        rewardInfo:false,
        //showSwiper:true,
        swiperArr:[],
      }  
    },
    computed: {
         
    },
    activated(){
