    },
    created(){
        this.initStyleFun()
        this.loopFun();
    },
    mounted(){
        let me = this
        window['skipReward'] = (userId) => {
            me.skipReward(userId) // 这个也就是我定义的方法
        }
        window['refreshMarchActivity'] = (userId) => {
            me.refreshMarchActivity(userId) // 这个也就是我定义的方法
        }
        window['shareFinish'] = () => {
            me.appShareFinish() // 这个也就是我定义的方法
        }
        this.initSwiper();
    },
    methods:{
        initSwiper(){
            setTimeout(()=>{
                this.swiperArr=[
                    {
                        phone:'158****5596',
                        reward:'破壁1',
