                        unit:'台'
                    },
                      {
                        phone:'158****5596',
                        reward:'破壁2',
                        unit:'台'
                    },
                    {
                        phone:'157****5596',
                        reward:'破壁机6',
                        unit:'台'
                    },
                      {
                        phone:'158****5596',
                        reward:'破壁2',
                        unit:'台'
                    },
                    {
                        phone:'158****5596',
                        reward:'厨房清洁6件套7',
                        unit:'台'
                    },
                      {
                        phone:'158****5596',
                        reward:'破壁2',
                        unit:'台'
                    },
                    {
                        phone:'159****5596',
                        reward:'煲仔锅8',
                        unit:'台'
                    },
                      {
                        phone:'158****5596',
