                        unit:'台'
                    },
                ]
                console.log('this.swiperArr',this.swiperArr.length)
                //  this.showSwiper=true
            },1000)
            
        },
        initStyleFun(){
            let _this=this;
            try{
                const totalHeight=window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight;;
                const screenWidth=document.body.clientWidth; 
                console.log('totalHeight',totalHeight,'document.body.clientWidth',document.documentElement.clientHeight)
            //     const lhjHeight=(screenWidth*1790)/1500;//图片像素比，1500宽，2348高
            //     this.homeStyle.lhjHeight=lhjHeight;
            // _this.$nextTick(()=>{
            //     console.log('手机屏幕高度---',totalHeight,'---老虎机高度盒子-iiiii--',this.$refs.lhjBox.offsetHeight,'lhjHeight--大图片----',lhjHeight)
            //     console.log('this.$refs.textBox.offsetHeight---上方文本高--',this.$refs.textBox.offsetHeight)
            //     const textBoxHeight=this.$refs.textBox.offsetHeight;//上方文本盒子
            //     const mainHeight=this.$refs.lhjBox.offsetHeight;
