//中间的盲盒---抖动--间隔6秒
.wobble-hor-bottom{animation:wobble-hor-bottom .8s both}
@keyframes wobble-hor-bottom{
    0%,100%{transform:translateX(0);transform-origin:50% 50%} 
    30%{transform:translateX(15px) rotate(6deg)}
    45%{transform:translateX(-15px) rotate(-3.6deg)}
    60%{transform:translateX(9px) rotate(2.4deg)}
    75%{transform:translateX(-6px) rotate(-1.2deg)}
}

//抽奖按钮+2，点我抽盲盒-动效
.heartbeat{animation:heartbeat 2s ease-in-out infinite both}
@keyframes heartbeat{from{transform:scale(1);transform-origin:center center;animation-timing-function:ease-out}10%{transform:scale(.91);animation-timing-function:ease-in}17%{transform:scale(.98);animation-timing-function:ease-out}33%{transform:scale(.87);animation-timing-function:ease-in}45%{transform:scale(1);animation-timing-function:ease-out}}
//奖品-动效
 .pulsate-bck{animation:pulsate-bck 3s ease-in-out infinite both}
 @keyframes pulsate-bck{0%{transform:scale(1)}50%{transform:scale(.9)}100%{transform:scale(1)}}
</style>



