<template>
    <div class="scroll_out">
         <div class="get_reward_box">
            <div class="get_img"><img :src="require('@/assets/images/618Activity/get.png')" alt=""></div>
            <div class="scroll_box">
                <template v-if="swiperArr.length"> 
                    <swiper :options="swiperOption" ref="cookingSwiper" style='width:100%;height:100%' id="scrollReward">
                        <swiper-slide v-for="(item,index) in swiperArr" :key="index" class="swiper-slide page">
                            <div class="single_swi">
                                恭喜
                                <span class="red_text">{{item.phone}}</span>
                                获得
                                <span class="red_text">{{item.reward}}</span>
                                {{`1${item.unit}`}}
                            </div>
                        </swiper-slide>
                    </swiper>
                </template>
            </div>
        </div>
    </div>
</template>
<script>
import { swiper, swiperSlide } from "vue-awesome-swiper";
import "swiper/css/swiper.css";  
export default {
    components:{
        swiper,
        swiperSlide,
    },
    props:{
        swiperArr:{
            type:Array,
            default:()=>[],
            require:true
        }
    },
    data() {
      return {
            swiperOption:{
                direction: 'vertical',
                    autoplay :{
                        delay:2000,
                        disableOnInteraction:false,
                        
                    },
                    loop:true,
                    loopAdditionalSlides:19,
                    loopedSlides:8,
                    slidesPerView: 3.5,
                    spaceBetween: 12,
                    observer: true,
                    observeParents: true,
                   
            },//swiper选项
      }  
    },
    computed: {
        swiper() {
            const swiper=this.$refs.cookingSwiper?this.$refs.cookingSwiper.swiper:null
            return swiper
        }
    },
    watch:{
        swiperArr(newArr){
            console.log('watch---swiperArr---变化---');
            if(this.swiper){
                var arr=[]
                newArr.forEach(item=>{
                    const html=`<swiper-slide class="swiper-slide page"><div class="single_swi">`
                    +`恭喜<span class="red_text" style="color:#CA5139;">${item.phone}</span>`
                     +`获得<span class="red_text" style="color:#CA5139;">${item.reward}</span>1${item.unit}</div></swiper-slide>`      
                     arr.push(html)
                })
                if(this.swiper){
                     this.swiper.removeAllSlides(); //移除全部
                    this.swiper.appendSlide(arr.join(','));
                    this.swiper.updateSlides();
                }
                
            }
        },
    },
    created(){
         
    },
    mounted(){
       //this.initSwiper()
        
    },
    methods:{
        initSwiper(){
            setTimeout(()=>{ 
                this.$nextTick(()=>{
                    // if(this.swiper){
                    //     return
                    // }
                    // this.swiper=new 
                    //console.log( ' this.$refs.cookingSwiper',this.$refs.cookingSwiper)
                    //this.$refs.cookingSwiper.swiper.init()
                })
                 
                // var mySwiper = new Swiper('#scrollReward', {
                //     ...this.swiperOption
                // })
                 
            },500)
             
        }
    }
}
</script>
<style lang="scss" >
  .lottery_box .notice-swipe {
    height: 40px;
    line-height: 40px;
  }
</style>
<style lang="scss" scoped>
.scroll_out{
    padding:18px 20px 16px;
}
  .get_reward_box{
    background: linear-gradient(0deg, #FFF7E7, #FFEBC1);
    border-radius: 30px;
    border:10px solid #fff;
    padding:0 36px 36px 36px;
    // margin:18px 20px 16px;
      .get_img{
          text-align:center;
          >img{
              width:239px;
          }
      }
      .scroll_box{
          background: rgba(255, 216, 194, 0.5);
          border-radius: 15px;
          height:257px;
          padding:15px 15px 6px;
          margin-top:20px;
          .single_swi{
                font-size: 30px;
                font-family: PingFang SC;
                font-weight: 400;
                color: #333333;
                padding-bottom:30px;
                // line-height: 120px;
                // height:120px;
                .red_text{
                    color:#CA5139;
                }
          }
      }
  }
.lottery_box{
    padding-top:100px;
    .gp_box{
        margin:0 30px;
        height:230px;
         
        border:1px solid #ddd;
    }
}
</style>



