<template>
    <div class="count_box">
        <!--活动进行中-->
        <div  v-if="isInActivityTime=='inner'">
            <van-count-down :time="duringTime" @finish="finishCount('end')">
                <template #default="timeData">
                    <div class="count_inner_box">
                        <div class="st_text_box">
                            <span class="colon">距离活动结束:</span>
                            <span class="block">{{ timeData.days<10?`0${timeData.days}`:timeData.days}}</span>
                            <span class="colon">天</span>
                            <span class="block">{{ timeData.hours<10?`0${timeData.hours}`:timeData.hours }}</span>
                            <span class="colon">时</span>
                            <span class="block">{{ timeData.minutes<10?`0${timeData.minutes}`:timeData.minutes }}</span>
                            <span class="colon">分</span>
                            <span class="block">{{ timeData.seconds<10?`0${timeData.seconds}`:timeData.seconds }}</span>
                            <span class="colon">秒</span>
                        </div>
                    </div>
                </template>
            </van-count-down>
        </div>
        <!--活动未开始-->
        <div  v-if="isInActivityTime=='before'">
            <van-count-down :time="beforeTime" @finish="finishCount('ready')" format="DD 天 HH 时 mm 分 ss 秒">
                <template #default="timeData">
                    <div class="count_inner_box">
                        <div class="st_text_box">
                            <span class="colon">距离活动开始:</span>
                            <span class="block">{{ timeData.days<10?`0${timeData.days}`:timeData.days}}</span>
                            <span class="colon">天</span>
                            <span class="block">{{ timeData.hours<10?`0${timeData.hours}`:timeData.hours }}</span>
                            <span class="colon">时</span>
                            <span class="block">{{ timeData.minutes<10?`0${timeData.minutes}`:timeData.minutes }}</span>
                            <span class="colon">分</span>
                            <span class="block">{{ timeData.seconds<10?`0${timeData.seconds}`:timeData.seconds }}</span>
                            <span class="colon">秒</span>
                        </div>
                    </div>
                </template>
            </van-count-down>
        </div>
        <!--活动已结束-->
        <div  v-if="isInActivityTime=='after'" class="act_text">
            <span>活动已结束</span>
        </div>
    </div> 
</template>
<script>
import {
  dateDuring
} from '@/utils'
export default {
    data() {
        return {
            beforeTime:'',//活动开始之前倒计时
            duringTime:'',//活动时间内倒计时时间戳
        }
    },
    props:{
        startTime:{
            type:String,
            default:'2022-06-10 14:07:00'
        },
        endTime:{
            type:String,
            default:'2022-07-07 17:00:00'
        },
        isInActivityTime:{
            type:String,
            default:'inner'
        }
    },
    watch:{
        isInActivityTime(newVal,oldVal){
            this.initTimeFun()
        }
    },
    created(){
        this.initTimeFun();
    },
    methods:{
         initTimeFun(){
            const startTime=this.startTime,endTime=this.endTime;    
            if(this.isInActivityTime =='before'){
                const time=dateDuring(startTime);
                this.beforeTime=time;
                console.log('距离开始---时间戳---',time);
            }
            if(this.isInActivityTime =='inner'){
                const time=dateDuring(endTime);
                this.duringTime=time;
            }
        },
        //活动未开始时-倒计时结束
        finishCount(type){
            if(type=='ready'){
                this.isInActivityTime='inner';
                const time=dateDuring(this.endTime);
                this.duringTime=time;
            }
            if(type=='end'){
                this.isInActivityTime='after';
            }
        },
    }
}
</script>
<style lang="scss" scoped>
      .count_box{
        font-size: 32px;
        font-family: PingFang SC;
        font-weight: 500;
        color: #FFFFFF;
        line-height: 52px;
        .act_text{
            display: flex;
            justify-content: center;
            //position: relative;
            >span{
                padding:4px;
                background-image: linear-gradient(to right, transparent,rgba(157, 51, 242, 0.5),transparent);
            }
        }
        .i_bg{
            //width:80%;
        }
        .count_inner_box{
            font-size: 32px;
            font-family: PingFang SC;
            font-weight: 500;
            color: #FFFFFF;
            display: flex;
            justify-content: center;
            .st_text_box{
                padding: 10px;
                background-image: linear-gradient(to right, transparent,#9D33F2,transparent);
            }
            .block{
                color:rgba(102, 1, 188, 1);
            }
        }
      }
</style>


